.car_main {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    margin-bottom: 80px;
    @media (max-width: 992px) {
        grid-template-columns: 1fr;
    }
    &__left {
       grid-column: span 2;
       @media (max-width: 992px) {
        padding-bottom: 40px;
       }
       .swiper {
           &__navigation {
               display: flex;
           }
           &-slider {
               width: 100%;
           }
           &_button {
               display: flex;
               justify-content: center;
               align-items: center;
               width: 40px;
               height: 40px;
               border: 1px solid $border_grey;
               border-radius: 3px;
               transition: 0.7s;
               cursor: pointer;
               &:hover {
                background-color: #000;
                color: #fff;
               }
           }
           &__pagination {
               display: flex;
               margin: 0 5px;
           }
           &-pagination {
            &-bullet {
                margin: 0 5px;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 40px;
                height: 40px;
                border: 1px solid $border_grey;
                border-radius: 3px;
                background-color: transparent;
                opacity: 1;
                @include text($pt400, 16px, $heading_dark, 16px);
                transition: 0.7s;
                &-active {
                    background-color: #000;
                    color: #fff;
                }
            }
           }
       }

    }
    &__card {
        display: flex;
        flex-direction: column;
        margin-bottom: 60px;
        img {
            margin-bottom: -134px;
            border-radius: 5px;
            width: 100%;

            @media (max-width: 576px) {
                margin-bottom: -90px;
            }
        }
        .card {

            &__description {
                border-radius: 5px;
                box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
                margin: 0 30px;
                background-color: #fff;
                padding: 30px;
                z-index: 999999;

                @media (max-width: 768px) {
                    margin: 0 15px;
                }
            }
            &__title {
                margin-top: 22px;
                margin-bottom: 15px;
            }
            &__text {
                @include text($pt400, 14px, $text_dark, 24px);
                margin-top: 14px;
            }
        }

    
    }
    &__right {
       padding-left: 70px;
       .card__title {
        font-size: 18px;
        margin-bottom: 23px;
    }

    @media (max-width: 992px) {
        padding-left: 0;
        width: 60%;
        margin: auto;
    }
    @media (max-width: 576px) {
        width: 90%;
    }
    }
    &__people {
        img {
            width: 100%;
            border-radius: 5px;
            margin-bottom: 20px;
        }
        .card__title {
            font-size: 18px;
            margin-bottom: 8px;
        }
        .card__text {
            @include text($pt400, 15px, $text_dark, 25px);
            margin-top: 14px;
            margin-bottom: 21px;
        }
        margin-bottom: 60px;
    }
    .popular_posts {
 
        &__card {
            display: block;
            margin-bottom: 40px;
            .card__image {
                position: relative;
            }
            .vehicle {
                position: absolute;
                left: 20px;
                top: 20px;
            }
            img {
                width: 100%;
                margin-bottom: 10px;
                border-radius: 5px;
            }
            .card__title {
                font-size: 20px;
                line-height: 28px;
                margin-bottom: 10px;
            }
        }
    }
    .instagram {
        margin-bottom: 60px;
        &__posts {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 10px;
            margin-bottom: 10px;
            img {
                width: 100%;
                border-radius: 3px;
            }
        }
    }
    .tags {
        margin-bottom: 60px;
        &__items {
          display: flex;
          flex-wrap: wrap;
          gap: 10px;
        }
        &__item {
            @include text($pt400, 14px, $heading_dark, 18.12px);
            background-color: $tag_bg;
            border-radius: 3px;
            padding: 7px 16px;
        }
    }

}
.email_newsletter {
    margin-bottom: 60px;
    .card__title {
        margin-bottom: 10px;
    }
    .card__text {
        @include text($pt400, 15px, $text_dark, 25px);
        margin-top: 14px;
        margin-bottom: 27px;
    }
    &__form {
        .input {
            border: 1px solid $border_grey;
            margin-bottom: 10px;
        }
    }
}