.navigation {
    z-index: 9999999999;
    position: relative;
    padding: 36px 0;
    background-color: $navigation;
    &-burger {
        display: none;
        margin-right: 20px;
        @media (max-width: 768px) {
            display: block;
           position: absolute;
           right: 0;
           .icon {
               z-index: 999999999;
           }
        }
    }
    &-menu {
        display: flex;
        justify-content: space-between;
        @media (max-width: 768px) {
            background-color: $navigation;
            position: fixed;
            flex-direction: column;
            align-items: center;
           right: 0;
           left: 0;
            top: -100%;
            transition: 0.7s;
        }
    }
    &__links {
        @media (max-width: 768px) {
            flex-direction: column;
            
        }
        display: flex;
    }
    &__link {
        @include text($pt400, 14px, #fff, 18.12px);
        margin: 0 20px;
        @media (max-width: 768px) {
            margin-bottom: 10px;
            display: block;
        }
    }
    &__logo {
        @include text($pt400, 14px, #fff, 18.12px);
        @media (max-width: 768px) {
            margin-bottom: 10px;
            display: block;
            margin-top: 10px;
        }
    }
    &__search {
        display: flex;
        @media (max-width: 768px) {
            margin-bottom: 10px;
        }
    }
    .icon {
        cursor: pointer;
        font-size: 20px;
        color: $icon_color;
    }
    &__input {
        border: none;
        outline: none;
        background-color: $button;
        margin-right: 10px;
        @include text($pt400, 14px, $input_text, 18.12px);
    }
}