$pt400: 'PTSans-Regular';
$pti400: 'PTSans-Regular-Italic';
$pt700: 'PTSans-Bold';
$pti700: 'PTSans-Bold-Italic';


$navigation: #000;
$icon_color: #929EAF;
$main_background: #F2F2F2;
$logo: #4A90E2;
$button: #232323;
$footer: #171717;
$gradient: linear-gradient(180deg, rgba(0, 0, 0,0.0075) 0%, rgba(0, 0, 0, 0.75) 82.82%);
$heading_dark: #171717;
$text_dark: #666;
$tag_bg: #EBEBEB;
$border_grey: #E5E5E5;
$input_text: #AEB3BB;
$footer_text: #CECECE;
$footer_item_bg: #373737;


@mixin text($font_family, $size, $color, $line_height) {
font-family: $font_family;
font-size: $size;
color: $color;
line-height: $line_height;
}
.button {
    display: block;
    text-align: center;
    width: 100%;
    background-color: $button;
    padding-top: 17px;
    padding-bottom: 15px;
    @include text($pt700, 14px, #fff, 18.12px);
    border-radius: 3px;
    cursor: pointer;
}
.input {
    width: 100%;
    padding-top: 17px;
    padding-bottom: 15px;
    padding-left: 15px;
    border-radius: 3px;
    @include text($pt400, 14px, $input_text, 18.12px);
    &::placeholder {
        @include text($pt400, 14px, $input_text, 18.12px);
    }
}
.vehicle {
    width: max-content;
    background-color: $logo;
    border-radius: 3px;
    @include text($pt400, 12px, #fff, 15.53px);
    padding: 2px 12px 4px 12px;
}
.vehicle_logo {
    padding: 3px 5px;
    text-transform: uppercase;
    font-size: 14px;
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
a, a:focus {
    text-decoration: none;
}
ul {
    list-style: none;
}
input, button {
    border: none;
    outline: none;
}


