.footer {
    background-color: $heading_dark;
    padding-top: 62px;
    .navigation {
        margin-top: 25px;
    }
    &_container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);

        @media (max-width: 992px) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media (max-width: 768px) {
            grid-template-columns: 1fr;
        }
    }
 
    &__text {
        @include text($pt400, 14px, $footer_text, 22px);
        max-width: 270px;
        @media (max-width: 768px) {
            max-width: 100%;
        }
    }
    .navigation__logo {
        margin-bottom: 28px;
        display: block;
    }
    .card__title {
        color: #fff;
        font-size: 20px;
    }
    &__socials {

        margin-top: 64px;
        &_items {
            display: flex;
            margin-top: 23px;
            gap: 15px;
        }
        &_item {
            width: 34px;
            height: 34px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $footer_item_bg;
            color: #fff;
            border-radius: 3px;
        }
        @media (max-width: 768px) {
            margin-bottom: 40px;
        }
    }
   &__posts {
       .card__title {
           font-size: 20px;
           margin-bottom: 28px;
       }
   }
   &_post {
       max-width: 293px;
       display: flex;
       margin-bottom: 25px;
       img {
           border-radius: 3px;
           margin-right: 20px;
       }
       .card__title {
           font-size: 15px;
           line-height: 26px;
           margin-bottom: 13px;
       }
       .card__details {
           color: $footer_text;
       }
       @media (max-width: 768px) {
        max-width: 100%;
        margin-bottom: 40px;
    }
   }
   
   &__newsletter {
       .card__title {
           margin-bottom: 38px;
       }
       .email_newsletter__form {
           max-width: 270px;
           .input {
               background-color: $footer_item_bg;
               border: none;
           }
           @media (max-width: 768px) {
            max-width: 100%;
        }
       }
       
   }
}