.most_popular {
    padding-bottom: 80px;
    .card__title {
        font-size: 30px;
        margin-bottom: 27px;
    }
    &__posts {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 30px;
        @media (max-width: 992px) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media (max-width: 992px) {
            grid-template-columns: 1fr;
        }
    }
    &__post {
        .card__image {
            position: relative;
        }
        .card__title {
            font-size: 20px;
            line-height: 28px;
            margin-bottom: 10px;
        }
        .vehicle {
            position: absolute;
            top: 24px;
            left: 24px;
        }
        img {
            border-radius: 5px;
            margin-bottom: 20px;
            width: 100%;
        }
    }
}