.car_products {
    padding-top: 30px;
    padding-bottom: 75px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    margin-bottom: 40px;
    @media (max-width: 992px) {
        grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 768px) {
        grid-template-columns: 1fr;
    }
    .card1 {
        grid-row: span 2;
    }
    &__card {
        display: block;
        position: relative;
        width: 100%;
        .card__image {
            border-radius: 3px;
            height: 100%;
            width: 100%;
        }
        .vehicle {
            position: absolute;
            margin: 30px 22px;
        }
        .card__description {
            border-radius: 3px;
            position: absolute;
           bottom: 0;
           padding: 69px 21px 23px 21px;
           background-image: $gradient;

           .card__title, .card__details {
            color: #fff;
        }
        }
       
    }
}