

@font-face {
    font-family: 'PTSans-Regular';
        src: url(../fonts/PTSans/PTSans-Regular.ttf);
}
@font-face {
    font-family: 'PTSans-Regular-Italic';
        src: url(../fonts/PTSans/PTSans-Italic.ttf);
}
@font-face {
    font-family: 'PTSans-Bold';
        src: url(../fonts/PTSans/PTSans-Bold.ttf);
}
@font-face {
    font-family: 'PTSans-Bold-Italic';
        src: url(../fonts/PTSans/PTSans-BoldItalic.ttf);
}
@font-face {
    font-family: 'FontAwesome';
    src: url(../fonts/fontawesome-webfont.woff2);
}

.icon {
    font-family: 'FontAwesome';
    font-style: normal;
    &__search::before {
        content: '\f002';
    }
    &__burger::before {
        content: '\f0c9';
    }
    &__times::before {
        content: '\f00d';
    }
    &__instagram::before {
        content: '\f16d';
    }
    &__left::before {
        content: '\f104';
    }
    &__right::before {
        content: '\f105';
    }
    &__facebook::before {
        content: '\f09a';
    }
    &__twitter::before {
        content: '\f099';
    }
    &__pinterest::before {
        content: '\f231';
    }
    &__googleplus::before {
        content: '\f0d5';
    }
}
